.more-ngen {
    &__header {
        margin-bottom: 30px;
    }

    &__title {
        padding-bottom: 22px;
        margin-bottom: 26px;
        border-bottom: 1px solid $text;
    }

    &__row {
        display: flex;
        margin: 0 -15px;

        @media (max-width: 991px) {
            margin: 0 -12px;
        }
    }

    &__col {
        flex: 1;
        padding: 0 15px;

        @media (max-width: 991px) {
            padding: 0 12px;

            // &:nth-child(n+5) {
            //     display: none;
            // }
        }

        // @media (max-width: 767px) {
        //     &:nth-child(n+4) {
        //         display: none;
        //     }
        // }

        // @media (max-width: 567px) {
        //     &:nth-child(n+3) {
        //         display: none;
        //     }
        // }
    }

    .slick {
        &-list {
            padding-bottom: 20px;
        }

        &-dots {
            display: flex;
            align-items: center;
            justify-content: center;

            li {
                width: auto;
                margin: 0;
                display: flex;
                align-items: center;
                justify-content: center;

                &:not(:last-child) {
                    margin-right: 15px;
                }

                button {
                    width: 22px;
                    height: 1px;
                    padding: 0;
                    background-color: $theme-dark;

                    &::before {
                        display: none;
                    }
                }

                &.slick-active {
                    button {
                        height: 2px;
                        background-color: $text;
                    }
                }
            }
        }
    }

    &__card {
        text-align: center;

        &-media {
            height: 150px;
            margin-bottom: 26px;

            @media (max-width: 567px) {
                height: 118px;
            }

            img {
                display: block;
                @include imgfit;
            }
        }

        &-title {
            font-size: 18px;
            font-weight: 800;
            margin-bottom: 20px;
            text-transform: uppercase;

            @media (max-width: 567px) {
                font-size: 14px;
            }
        }

        &-list {
            padding-left: 0;
            margin: 0;
            list-style-type: none;

            li {
                font-size: 14px;

                @media (max-width: 567px) {
                    font-size: 12px;
                }

                &:not(:last-child) {
                    margin-bottom: 15px;

                    @media (max-width: 567px) {
                        margin-bottom: 5px;
                    }
                }
            }
        }
    }
}